<template>
<div class="ml-4  mt-5">

    <h2>{{title}}</h2>
    <div class="mt-5">
          <el-table :data="all_cust.filter(data => !search || data.tags.toLowerCase().includes(search.toLowerCase()))" stripe size="medium" style="width: 100% cursor:pointer">
              <el-table-column label="Nom" prop="name">
              </el-table-column>
              <el-table-column label="Prenom" prop="surname">
              </el-table-column>
              <el-table-column label="Telephone" prop="phone">
              </el-table-column>
              <el-table-column label="email" prop="email">
              </el-table-column>
              <el-table-column align="right">

                  <template slot="header" slot-scope="scope">
                    <el-input  v-model="search"  size="mini" placeholder="Type to search"/>
                  </template>
                  <template slot-scope="scope">
                    <!-- <el-button icon="el-icon-view" circle></el-button> -->
                    <el-button icon="el-icon-edit" @click="handleEdit(scope.row)" circle></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)" circle></el-button>
                  </template>
              </el-table-column>
          </el-table>
    </div>


     <!-- Card ajout -->
    <ui-modal ref="edit" size="medium" title="Modification du services">

      <el-form ref="form" :model="form" enctype="multipart/form-data">
          <el-form-item label="Nom">
                  <el-input v-model="form.name"></el-input>
          </el-form-item>

          <el-form-item label="Prenom">
            <el-input v-model="form.surname" show-word-limit></el-input>
          </el-form-item>

          <el-form-item label="Email">
                  <el-input v-model="form.email"></el-input>
          </el-form-item>

          <el-form-item label="Telephone">
                  <el-input v-model="form.phone"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmitEdit">Enregirtrer</el-button>
            <el-button>Annuler</el-button>
          </el-form-item>
      </el-form> 
    </ui-modal>
</div>
</template>
<script>

    import axios from 'axios';
    // import moment from 'moment'; 
    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';

    export default {
        data() {
            return {
                // lang
                title: this.$t('pages.customer.customer_consult.title'),

                all_cust : [],
                search: '',
                form : {
                    code_postal : "",
                    email : "",
                    id: null,
                    name: "",
                    phone: null,
                    profile: null,
                    quartier : "",
                    rue: "",
                    sexe : "",
                    surname : "",
                    tags : "",
                    type_id: null,
                    username : "",
                    ville : "",
                },
            }
        },

        created() {
            this.initData()
        },

        mounted() {

        },

        methods: {
            async initData() {
                NProgress.start();
                try {

                    const response = await axios.get('xpress/client/getAllUser');
                    this.all_cust = response.data.clients;
                    console.log(this.all_cust);
                } 
                catch (error) {
                        this.$notify({
                        type: 'error',
                        title: "Echec de recuperation"
                    });
                }
                NProgress.done();
            },

            handleEdit(data){
        
                this.$refs['edit'].open();
                this.form = data;
            },

            handleDelete(data){

            this.$confirm('Suprimer ?', 'Alerte', {
                confirmButtonText: 'OK',
                cancelButtonText: 'Annuler',
                type: 'warning'

                }).then(() => {

                    NProgress.start();

                    axios.post('xpress/client/delete', data);
                    this.$notify({
                    title: 'Supprimer',
                    type: 'success'
                    });

                this.initData();

                }).catch(() => {
                this.$notify({
                    title: 'Echec de la suppression',
                    type: 'error'
                });
                NProgress.done();       
                });

            },

            onSubmitEdit(){
                // console.log(this.form);
                this.updateService(this.form);
                this.$refs['edit'].close();
            },

            async updateService(data) {

                NProgress.start();
                try {
                    console.log(data);
                    await axios.post('xpress/client/update', data);
                    this.$notify({
                        type: 'success',
                        title: "Enregistrer"
                    });

                } 
                catch (error) {
                        this.$notify({
                        type: 'error',
                        title: "Echec d'enregistrement"
                    });
                }
                this.initData();
                NProgress.done();
            },
        
        },
    }
</script>